#header {
  position:absolute;
  z-index:200;
}

.navbar {
  color:#333;
  margin: auto;
  background: #f3f3f3;
  height: 40px;
  position: fixed;
  top: 0;
  -webkit-transition: top 0.2s ease-in-out;
  -o-transition: top 0.2s ease-in-out;
  transition: top 0.2s ease-in-out;
  width: 100%;
}

.nav-up {
  top: -40px;
}

.name {
  display: inline-block;
  width: 40%;
  text-align: center;
}

.button-group {
  display: inline-block;
  width: 20%;
  margin-left:25vw;
}



